import React from "react";
import styled from "styled-components";
import Foryou from "../Foryou";

const Container = styled.div`
  background: #a1827842;
  backdrop-filter: blur(2px);
  width: 100vw;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
`;

const ForUSection = () => {
  return (
    <Container>
      <Foryou />
    </Container>
  );
};

export default ForUSection;
